.item {
  width: 100%;
  opacity: 0.6;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 8px;

  svg {
    cursor: move;
  }

  &_draging {
    opacity: 1;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 8px;

    .dragIcon {
      svg {
        cursor: move;
      }
    }

  }
}